*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: black;
  height:100vh;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
  padding: 0 8rem;
}

.navbar{
  padding: 0 8rem;
}
.navbar_links{
  display: flex;
  gap: 3.5rem;
  align-items: center;
}


.content{
  display: flex;
  justify-content: center;
  background: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35)), url('./../image.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 95vh;
  background-attachment: fixed;
}

.home_content{
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 3rem;
  font-family: "Cabin";
}

.contact_button{
  background-color: #E6C7EB;
  padding: 15px;
  color: black;
  border: none;
}

.button{
  margin-top: -1.2rem;
}

.talk_container{
  margin-top: 12rem;
  padding: 0 12rem;
}

.talk_content{
  display: flex;
  justify-content: center;
  gap: 3rem;
  border: 1px solid #E6C7EB;
}

.talk_image img{
  width: 34rem;
  height: 41rem;
  border: 1px solid pink;
  object-fit: cover;
}

.lets_talk{
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: center;
  padding-left: 4rem;
}

.lets_talk p{
  font-size: 20px;
  line-height: 1.8em;
  padding-right: 4rem;
  font-family: 'Cormorant Garamond';
}

.work_with_us{
  margin-top: 12rem;
  padding: 0 11rem;
}

.work_content{
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: repeat(3,1fr);
  margin: 6rem auto;
  gap: 4rem;
  position: relative;
}

.socials a{
  color: white;
}

.slide2::before{
    content: "";
    position: absolute;
    height: calc(100% + 5.5rem);
    width: 2px;
    background: white;
    bottom: -2rem;
    left: 32%;
}
.slide2::after{
    content: "";
    position: absolute;
    height: calc(100% + 5.5rem);
    width: 2px;
    background: white;
    bottom: -2rem;
    left: 67%;
}

.slide p{
  margin-top: 2rem;
  padding: 0 1.5rem;
  line-height: 1.8em;
  font-size: 20px;
  font-family: 'Cormorant Garamond';
}

.slide h2{
  color: #E6C7EB;
}

.footer{
  margin-top: 10rem;
}

.contact_us{
  display: flex;
  align-items: center;
  gap: 12rem;
}

.form_group{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.name_email{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.socials{
  margin: 3rem auto;
  color: white;
  padding-bottom: 3rem;
  cursor: pointer;
}

.fa-instagram{
  margin-right: 2rem;
}

input, textarea{
  padding: 0.5rem 1rem;
}

.submit_button{
  background-color: black;
  color: #E6C7EB;
  border: 1px solid #E6C7EB;
  padding: 0.6rem 0;
}

.meet{
  padding: 0.5rem 1rem;
  background-color: black;
  border: 1px solid #E6C7EB;
  color: white;
  white-space: nowrap;
}

.meet_button{
  padding: 0.5rem 1rem;
  background-color: #E6C7EB;
  border: 1px solid #E6C7EB;
  color: black;
  white-space: nowrap;
}

.gpt3__navbar-menu_container-links-sign {
  display: none;
}

.gpt3__navbar-menu {
  margin-left: 1rem;

  display: none;
  position: relative;
}

.about_us{
  text-align: center;
  margin: 0 auto;
  width: 100%;
}



.nav_image{
  width: 14vw;
}

.about_us{
  max-width: 980px;
  overflow-wrap: break-word;
}

.about_us p{
  margin-top: 1rem;
  font-family: "Cormorant Garamond";
}

.about_us h1{
  font-size: 4rem;
}

@media screen and (max-width: 1200px) {
  .contact_us{
    gap: 1rem;
  }

  .talk_container{
    padding: 0 4rem;
  }
}

@media screen and (max-width: 1024px) {

.header, .navbar{
    padding: 0 4rem;
  }

  .content{
    width: 100%;
    height: auto;
    background-size: contain;
  }

   .contact_us{
    flex-direction: column;
    gap: 6rem;
    padding-bottom: 3rem;
   }

  .talk_content{
    flex-direction: column;
    border: none;
  }

  .lets_talk, .talk_container, .work_with_us{
    padding: 0;
  }

  .work_content{
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }

  .slide2::after,.slide2::before{
    content: none;
  }

  .lets_talk p{
    text-align: center;
    padding: 0 4rem;
  }

  .lets_talk{
    align-items: center;
  }

  .talk_image img{
    width: 100%;
    height: 30rem;
  }

  .home_content{
    padding: 1.5rem;
  }

  .slide p{
    padding: 0 8rem;
  }

  .home_content h2{
    font-size: 32px !important;
  }

}

@media screen and (max-width: 550px) {
  .navbar {
      padding: 2rem;
      flex-wrap: nowrap;
      padding-left: 0;
  }

  .nav_image{
    width: 150px;
  }

  .header{
    padding: 0;
  }

  .about_us p{
    max-width: 300px;
    margin: 0 auto;
  }

.navbar_links {
    display: none;
}

.gpt3__navbar-menu {
    display: flex;

}
  .meet {
      display: none;
  }

  .gpt3__navbar-menu_container {
      position: absolute;
      top: 26px;
      right: 26px;
      background-color: black;
      border-radius: 5px;
      padding: 1rem;
  }

  .gpt3__navbar-menu_container a{
    text-decoration: none;
    color: white;
  }

  .gpt3__navbar-menu_container-links-sign {
      display: block;
  }

  .content{
    width: 100%;
    /* height: auto; */
    background-size: cover;
    min-height: 71vh;
  }

  .home_content h2{
    font-size: 35px !important;
  }

  .home_content h4{
    font-size: 25px !important;
  }

  .home_content{
    gap: 1rem;
  }

  .name_email{
    flex-direction: column;
    align-items: normal;
  }

  .talk_image img,.contact_image img{
    width: 100%;
    height: 23rem;
  }

  .talk_content,.talk_image img{
    border: none;
  }

  .lets_talk{
    text-align: center;
  }

  .lets_talk p{
    padding: 0 2rem;
  }

  .talk_container,.work_with_us{
    margin-top: 4rem;
  }

  .button{
    margin-top: 1.5rem;
  }

  .work h2{
    font-size: 40px !important;
  }

  .footer{
    margin-top: 5rem;
    padding-bottom: 2rem;
  }

  .contact_us{
    gap: 3rem;
  }

  .slide p{
    word-break: normal;
    padding: 0 1.35rem;
  }
}